<template>
  <div id="input">
    <main>
      <section class="user">
        <h3>Expenses</h3>
      </section>
      <form>
        <select name="date" id="date" class="date">
          <option v-for="(item, index) of options.date" v-bind:value="item" :key="index">
            {{item}}
          </option>
        </select>
        <input type="text" id="name" class="input" placeholder="Name" v-model="name">
        <input type="text" id="price" class="input" placeholder="Price" v-model.number="price">
        <input type="text" id="note" class="input" placeholder="Note" v-model="note">
        <select name="category" id="category" class="category">
          <option v-for="(item, index) of options.category" v-bind:value="item" :key="index">
            {{item.en}}
          </option>
        </select>
      </form>
      <div class="sender">
        <p></p>
        <button>send</button>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  name: "InputForm",
  props: {
    ttl: String
  },
  data() {
    return {
      date: '',
      name: '',
      price: '',
      note: '',
      category: '',
      country: '',
      today: '',
      options: {
        category: [
          {en: 'Food', jp: '食費', number: 0},
          {en: 'Transport', jp: '交通費', number: 1},
          {en: 'Socializing', jp: '交際費', number: 2},
          {en: 'Tech', jp: '機会', number: 3},
          {en: 'Clothes', jp: '衣服', number: 4},
          {en: 'Beauty', jp: '美容', number: 5},
          {en: 'Medical', jp: '医療・健康', number: 6},
          {en: 'Education', jp: '教育', number: 7},
          {en: 'Entartainments', jp: '娯楽', number: 8},
          {en: 'Payments', jp: '支払', number: 9},
          {en: 'Others', jp: 'その他', number: 10},
          ],
          date: []
      }
    };
  },
  methods: {
    createDateOptions: function() {
      const date = new Date();
      this.options.date.push(this.formatDate(date))
      for (var i = 0; i < 5; i++) {
        date.setDate(date.getDate() - 1)
        this.options.date.push(this.formatDate(date))
      }
    },
    formatDate: function(input) {
      return `${input.getFullYear()}-${this.ifZero(input.getMonth() + 1)}-${this.ifZero(input.getDate())}`;
    },
    ifZero: function(input) {
      const result = input < 10 ?  `0${input}` : String(input);
      return result
    }
  },
  created() {
    this.createDateOptions()
  }
};
</script>

